#profile-img {
    width: 400px;
}

.about-container, .like-dislike-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
    padding: 3vh 15vw 3vh 15vw;
}

.about-img-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-text em {
    color: var(--blue);
}

.about-text a {
    color: var(--dark-yellow);
    text-decoration-line: underline;
}

.about-list, .like-dislike-container {
    margin-bottom: 10px;
}

.about-text p {
    margin: 0;
    padding: 0;
    line-height: 1.5;
    font-size: 1rem;
}

.about-text h1, .about-text h5 {
    margin: 5px 0;
    padding: 5px 0;
    line-height: 1.5;
}

@media screen and (max-width: 1200px) {
    .about-container {
        margin: 0 2vw 0 2vw;
    }
}

@media screen and (max-width: 992px) {
    .about-content, .like-dislike-container{
        grid-template-columns: 1fr;
    }   
    
    .about-container {
        margin: 0 5px 0 5px;
    }
}

@media screen and (max-width: 768px) {
    #profile-img {
        max-width: 80vw;
    }    
}