#header-line {
    width: 35vw;
    margin-left: 0;
}

.header-image, #affinity {
    width: 100%;
    max-width: 100%;
    max-height: 100%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.project > * {
    padding-left: 20vw;
    padding-right: 20vw;
    padding-top: 4vh;
    padding-bottom: 4vh;
    margin-top: 20px;
    margin-bottom: 20px;
}

.project h3,
.project h5 {
    font-weight: 700;
}

.project a {
    font-weight: 600;
    color: var(--black);
}

div.quote {
    margin-top: 2vh;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    padding: 10px 20px 10px 20px;
    border-radius: 10px;
}

div.quote p {
    /* font-style:italic; */
    font-weight: 600;
    font-size: 1.2rem;
    margin-left: 20px;
}

.project-summary-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 10px;
    max-width: 100%;
    padding-top: 5vh;
    padding-bottom: 5vh;
}

.project-summary-container p,
.overview-sections p {
    margin: 0;
}

.center-card {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    text-align: flex-start;
    padding: 10px 20px 10px 20px;
    margin-top: 3vh;
    border-radius: 10px;
    /* box-shadow: 0px 3px 5px lightgrey; */
}

.center-card img {
    margin-right: 20px;
}

.project-summary-container section,
.overview-sections {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    padding: 5px;
    margin: 0 10px 0 10px;
}

.row-card {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 3vh 1vw;
}

.row-card > div {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    padding: 5px;
    margin: 0 10px 0 10px;
}

.row-card > div > p {
    font-size: 1rem;
}

.row-card > div > h2 {
    margin: 5px;
}

.icon {
    width: 3.5vw;
    min-width: 40px;
}

.sub-container,
.vertical-wrap-list {
    margin-left: 3vw;
    margin-right: 3vw;
    padding-top: 2vh;
    padding-bottom: 2vh;
}

.vertical-wrap-list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-top: 3vh;
    align-items: flex-start;
    max-height: 35vh;
}

.questions-list div {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding: 10px;
    align-items: center;
}


.questions-list p {
    margin-left: 20px;
}

.mini-side-nav {
    z-index: 999;
    position: fixed;
    top: 30%;
    right: 20px;
    /* width: 100%; */
    height: 100vh;
}

.mini-side-nav a {
    /* text-shadow: 0px 0px 1px rgba(0, 0, 0, 0.5); */
    color: var(--grey);
    text-decoration: none;
}

.mini-side-nav a:hover {
    color: var(--black);
}

.impact-section h3 {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 10px;
}

.impact-section p{
    font-size: 1.5rem;
    font-weight: 500;
}

.desktop-img, .mobile-img, .brainstorm-img{
    display: flex;
    align-items: center;
    margin-top: 3vh;
    margin-bottom: 3vh;
    flex-wrap: wrap;
    flex-direction: row;
}

.desktop-img, .mobile-img {
    justify-content: flex-start
}

.full-img {
    justify-content: center;
}

.desktop-img > img, .mobile-img > img, .full-img > img {
    border-radius: 3px;
    border-color: var(--black);
    border-width: 1px;
    border-style: solid;
}

.full-img > img, #styleguide {
    width: 80%;
    margin: 5px 20px;
    min-width: 600px;
}

.desktop-img > img {
    width: 400px;
    margin: 5px 15px;
}

.mobile-img > img {
    width: 150px;
    margin: 5px 20px;
}

@media screen and (max-width: 992px) {

    .project-opener-split {
        display: flex;
        flex-direction: column;
    }

    .project>div {
        padding-left: 10vw;
        padding-right: 10vw;
    }
    .mini-side-nav {
        display: none;
    }

}

@media screen and (max-width: 768px) {
    .project-summary-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .project-summary-container div {
        margin-bottom: 10px;
    }

    .project>div {
        padding-left: 5vw;
        padding-right: 5vw;
    }

    .vertical-wrap-list {
        flex-wrap: nowrap;
        max-height: none;
    }

    .row-card {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}