:root {
    --red: #F34E4E;
    --yellow: #FCC32F;
    --blue: #3F8CFF;
    --black: #252525;
    --light-red: #FFD4D4;
    --light-yellow: #FFF0CB;
    --light-blue: #CCE0FF;
    --dark-red: #A71919;
    --dark-yellow: #BB681A;
    --dark-blue: #103771;
    --white: #FFFFFF;
    --grey: #999999;

}

body {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: var(--black);
}

.title,
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Rokkitt', serif;
    font-weight: 600;
    letter-spacing: 1px;
}

h1 {
    font-size: 3rem;
}

h2 {
    font-size: 2.5rem;
}

h3 {
    font-size: 2rem;
}

h4 {
    font-size: 1.5rem;
}

h5 {
    font-size: 1.2rem;
}

h6 {
    font-size: 1rem;
}

p {
    font-size: 1.1rem;
}

b {
    font-weight: 600;
}

.resume-container, .play-container {
    padding: 3vh 20vw 3vh 20vw;
}

@media screen and (max-width: 768px) {
    .resume-container, .play-container {
        padding: 3vh 5vw 3vh 5vw;
    }
  }