.eduroom .project h3{
    color: var(--blue);
}

.eduroom .center-card, .eduroom .quote, .eduroom .project-summary-container  {
    background-color: var(--light-blue);
}

.eduroom .center-card h5, .eduroom .project h4,
.eduroom .project h6 { 
    color: var(--dark-blue);
}

#mockup, #affinity img {
    width: 100%;
}

.process-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

.eduroom .process-container a {
    text-decoration-color: var(--blue);
}

.eduroom .impact-section {
    background-color: var(--dark-blue);
}

.eduroom .impact-section * {
    color: var(--white);
}