.transloc .project h3 {
    color: var(--yellow);
}

 .transloc .center-, .transloc .project-summary-container, .transloc .quote {
    background-color: var(--light-yellow);
}

.transloc .center-card h5 { 
    color: var(--dark-yellow);
}

#prototype {
    border-radius: 10px;
}

.pros-cons {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
    width: 100%;
    margin: 1vhvh 0 3vh 0;
}

.transloc .personas {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 7vw;
    width: 100%;
    margin: 1vh 0 3vh 0;
}

.transloc .personas img {
    width: 100%;
}
