.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2vh 0 2vh 0;
    padding: 2vh 0 2vh 0;
}

header {
    width: 100%;
    max-width: 80%;
    margin-right: auto;
    margin-left: auto;
}

nav {
    display: flex;
    align-items: center;
    margin: 2px;
    justify-content: space-between;
    padding: 5px;
    background-color: transparent;
    margin-left: auto;
    z-index: 12;
}

.menus {
    display: flex;
    align-items: center;
    list-style: none;
    margin: 0;
    padding: 0;
}

li {
    color: var(--black);
    display: flex;
    align-items: center;
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: 500;
    padding: 5px 15px 5px 15px;
    margin: 0 15px 0 15px;
    line-height: 20px;
    cursor: pointer;
    /* font-family: 'Rokkitt', serif; */
}

button {
    color: var(--black);
    display: flex;
    align-items: center;
    text-decoration: none;
    font-size: 1.2rem;
    padding: 0;
    margin: 0;
    line-height: 20px;
    cursor: pointer;
    background-color: transparent;
    border: 0;
    /* font-family: 'Rokkitt', serif; */
}

nav a {
    color: var(--black);
    text-decoration: none;
}

nav a:active {
    font-weight: 800;
}

.hamburger-menu {
    display: none;
}

.logo-container a {
    display: flex;
    flex-direction: row;
    text-decoration: none;
    align-items: center;
    color: var(--black);
}

.logo-container h5 {
    margin-left: 20px;
}

@media screen and (max-width: 768px) {
    .menus {
        display: none;
    }

    .hamburger-menu {
        display: block;
        cursor: pointer;
        width: 100%;
        height: 100%;
    }
}